import Categories from "../components/Categories";
import Herodiv from "../components/Hero";
import { Inner } from "../components/Page";
import styled from "styled-components";

const Hero = styled(Inner)`
  && {
    max-width: 100vw;
    img {
      height: 30vh;
    }
  }
`;

const Home = props => (
  <div>
    <Herodiv className={props.className} />
    <Categories />
  </div>
);

export default Home;
